<template>
    <div class="basicProps">
        <div class="basicProps-header">基本属性</div>
        <el-table :data="basicProps" width="100%" size="mini" stripe show-summary :header-cell-style="{backgroundColor: '#95c1e8',color: '#fff'}">
          <el-table-column label="属性" prop="props" align="center" width="82"></el-table-column>
          <el-table-column label="数值" prop="num" align="center"></el-table-column>
        </el-table>
        <div class="tip-container">
          <div class="basic-tip">保存前请先计算</div>
        </div>
        <div class="basicProps-footer">
            <el-button type="success" @click="countBasicProps" size="mini">计算</el-button>
          <el-button type="primary" @click="submitBasicProperty" size="mini">保存</el-button>
        </div>
    </div>
  </template>

<script>
export default {
  data () {
    return {
      basicProps: [
        {
          props: '力量(STR)',
          num: 0
        },
        {
          props: '体型(SIZ)',
          num: 0
        },
        {
          props: '教育(EDU)',
          num: 0
        },
        {
          props: '体质(CON)',
          num: 0
        },
        {
          props: '敏捷(DEX)',
          num: 0
        },
        {
          props: '外貌(APP)',
          num: 0
        },
        {
          props: '意志(POW)',
          num: 0
        },
        {
          props: '智力(INT)',
          num: 0
        },
        {
          props: '幸运(LUK)',
          num: 0
        }
      ]
    }
  },
  methods: {
    submitBasicProperty () {
      this.$store.commit('setBasicProps', this.basicProps)
      alert('信息已保存')
    },
    countBasicProps: function () {
      const basicProps = this.basicProps
      basicProps[0].num = this.$store.state.propertyRoll.STR - this.$store.state.propertyCorrection.STR
      basicProps[1].num = this.$store.state.propertyRoll.SIZ - this.$store.state.propertyCorrection.SIZ
      basicProps[2].num = this.$store.state.propertyRoll.EDU - this.$store.state.propertyCorrection.EDU
      basicProps[3].num = this.$store.state.propertyRoll.CON - this.$store.state.propertyCorrection.CON
      basicProps[4].num = this.$store.state.propertyRoll.DEX - this.$store.state.propertyCorrection.DEX
      basicProps[5].num = this.$store.state.propertyRoll.APP - this.$store.state.propertyCorrection.APP
      basicProps[6].num = this.$store.state.propertyRoll.POW
      basicProps[7].num = this.$store.state.propertyRoll.INT
      basicProps[8].num = this.$store.state.propertyRoll.LUK
      return basicProps
    }
  }
}
</script>

<style scoped>
  .basicProps-header {
  background-color: #A0D5F6;
  color: #00243F;
  height: 36px;
  line-height: 36px;
  border-radius: 15px 15px 0 0;
  text-align: center;
  }
  .basicProps-footer {
  padding: 5px;
  background-color: #fff;
  text-align: center;
  border-radius: 0 0 15px 15px;
  }
  .tip-container {
    background-color: #fff;
    padding-top: 23px;
  }
  .basic-tip {
    text-align: center;
    background-color: #fff;
    color: #ff0606;
  }
</style>



