<template>
  <div class="invCard">
    <el-row>
      <el-col :span='18'>
        <div class="invMessage">
      <div class="invMessage-top">
        <div class="invMessage-top-left">
          <div class="invBasic">
            <div class="header">调查员信息</div>
            <div class="ul-container">
              <ul>
              <li>姓名:{{invMessage.invName}}</li>
              <li>玩家:{{invMessage.playerName}}</li>
              <li>职业:{{invMessage.invCareer}}</li>
              <li>年龄:{{invMessage.invAge}}</li>
              <li>性别:{{invMessage.invGender}}</li>
              <li>住地:{{invMessage.invAddress}}</li>
            </ul>
            </div>
          </div>
          <div class="invCredit">
            <div class="header">资产</div>
            <div class="ul-container">
              <ul>
              <li>信誉:{{invMessage.invCredit}}</li>
              <li>生活水平:{{invMessage.invLifeLevel}}</li>
              <li>现金:{{invMessage.invCash}}</li>
              <li>其他资产:{{invMessage.invDeposit}}</li>
              <li>消费水平:{{invMessage.invConsumptionLevel}}</li>
            </ul>
            </div>
          </div>
        </div>
        <div class="invMessage-top-right">
          <div class="invBackground bgColor">
            <div class="header">背景故事</div>
            <div class="invBackground-list">
              <div class="ul-container invBackground-ul">
              <ul>
                <li>出身:{{invMessage.invBackgrounds.invBackground}}</li>
                <li>形象描述:{{invMessage.invBackgrounds.imageDescribe}}</li>
                <li>思想与信念:{{invMessage.invBackgrounds.invBelief}}</li>
                <li>重要之人:{{invMessage.invBackgrounds.importPerson}}</li>
                <li>重要之地:{{invMessage.invBackgrounds.importPlace}}</li>
              </ul>
            </div>
            <div class="ul-container invBackground-ul">
              <ul>
                <li>宝贵之物:{{invMessage.invBackgrounds.importTreasure}}</li>
                <li>特质:{{invMessage.invBackgrounds.invPeculiarity}}</li>
                <li>伤疤:{{invMessage.invBackgrounds.invScar}}</li>
                <li>恐惧之物:{{invMessage.invBackgrounds.invFear}}</li>
                <li>同伴:{{invMessage.invCompanions}}</li>
              </ul>
            </div>
            </div>
            <ul>
              <li>其他:{{invMessage.invBackgrounds.invOther}}</li>
            </ul>
          </div>
        </div>
      </div>
      <div class="invMessage-mid">
        <div class="invMessage-mid-top">
          <div class="header">基本属性</div>
          <div class="table-container">
            <el-table
              :data="invBasicProp"
              size="mini"
              :header-cell-style="{backgroundColor: '#95c1e8',color: '#fff'}">
              <el-table-column label="力量" min-width="50" prop="STR" align="center"></el-table-column>
              <el-table-column label="体型" min-width="50" prop="SIZ" align="center"></el-table-column>
              <el-table-column label="教育" min-width="50" prop="EDU" align="center"></el-table-column>
              <el-table-column label="体质" min-width="50" prop="CON" align="center"></el-table-column>
              <el-table-column label="敏捷" min-width="50" prop="DEX" align="center"></el-table-column>
              <el-table-column label="外貌" min-width="50" prop="APP" align="center"></el-table-column>
              <el-table-column label="意志" min-width="50" prop="POW" align="center"></el-table-column>
              <el-table-column label="智力" min-width="50" prop="INT" align="center"></el-table-column>
              <el-table-column label="幸运" min-width="50" prop="LUK" align="center"></el-table-column>
            </el-table>
          </div>
        </div>
        <div class="invMessage-mid-bottom">
          <div class="header">衍生属性</div>
          <div class="table-container">
            <el-table
            :data="invDeriveProp"
            size="mini"
            :header-cell-style="{backgroundColor: '#95c1e8',color: '#fff'}">
            <el-table-column min-width="50" label="生命" prop="HP" align="center"></el-table-column>
            <el-table-column min-width="50" label="魔法" prop="MA" align="center"></el-table-column>
            <el-table-column min-width="50" label="体格" prop="PH" align="center"></el-table-column>
            <el-table-column min-width="50" label="移动" prop="MOV" align="center"></el-table-column>
            <el-table-column min-width="50" label="理智" prop="SAN" align="center"></el-table-column>
            <el-table-column min-width="50" label="闪避" prop="DOD" align="center"></el-table-column>
            <el-table-column min-width="50" label="伤害加值" prop="DB" align="center"></el-table-column>
          </el-table>
          </div>
        </div>
      </div>
      <div class="invMessage-bottom">
          <div class="header">武器</div>
          <div class="table-container">
            <el-table
            :data="invWeapon"
            size="mini"
            height="100%"
            :header-cell-style="{backgroundColor: '#95c1e8',color: '#fff'}">
            <el-table-column min-width="50" label="武器" prop="weapon" align="center"></el-table-column>
            <el-table-column min-width="50" label="成功率" prop="success" align="center"></el-table-column>
            <el-table-column min-width="50" label="伤害" prop="damage" align="center"></el-table-column>
            <el-table-column min-width="50" label="射程" prop="range" align="center"></el-table-column>
            <el-table-column min-width="50" label="次数" prop="time" align="center"></el-table-column>
            <el-table-column min-width="50" label="弹药" prop="ammo" align="center"></el-table-column>
            <el-table-column min-width="50" label="故障率" prop="fault" align="center"></el-table-column>
          </el-table>
          </div>
      </div>
    </div>
      </el-col>
      <el-col :span="6">
        <div class="invSkill">
      <div class="skill-container">
        <el-table
          :data="checkbox"
          size="mini"
          height=100%
          :header-cell-style="{backgroundColor: '#A0D5F6',color: '#00243F'}"
          highlight-current-row
          stripe>
          <el-table-column label="技能" min-width="70" prop="name" align="center"></el-table-column>
          <el-table-column label="普通" min-width="50" prop="total" align="center"></el-table-column>
          <el-table-column label="困难" min-width="50" prop="hard" align="center"></el-table-column>
          <el-table-column label="极难" min-width="50" prop="hardest" align="center"></el-table-column>
        </el-table>
      </div>
    </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data () {
    return {
      invMessage: {},
      checkbox: [],
      invBasicProp: [{
        STR: '',
        SIZ: '',
        EDU: '',
        CON: '',
        DEX: '',
        APP: '',
        POW: '',
        INT: '',
        LUK: ''
      }],
      invDeriveProp: [{
        MOV: 0,
        SAN: 0,
        PH: 0,
        HP: 0,
        MA: 0,
        DOD: 0,
        DB: ''
      }],
      invWeapon: []
    }
  },
  created () {
    this.getMessage()
    this.getBasicProp()
    this.getDeriveProp()
    this.getWeapon()
    this.getCheckbox()
  },
  methods: {
    getMessage () {
      this.invMessage = this.$store.state.investigator.invMessage
    },
    getBasicProp () {
      let IP = this.$store.state.investigator.invProperty
      this.invBasicProp[0].STR = `${IP.STR}/${this.hard(IP.STR)}/${this.hardest(IP.STR)}`
      this.invBasicProp[0].SIZ = `${IP.SIZ}/${this.hard(IP.SIZ)}/${this.hardest(IP.SIZ)}`
      this.invBasicProp[0].EDU = `${IP.EDU}/${this.hard(IP.EDU)}/${this.hardest(IP.EDU)}`
      this.invBasicProp[0].CON = `${IP.CON}/${this.hard(IP.CON)}/${this.hardest(IP.CON)}`
      this.invBasicProp[0].DEX = `${IP.DEX}/${this.hard(IP.DEX)}/${this.hardest(IP.DEX)}`
      this.invBasicProp[0].APP = `${IP.APP}/${this.hard(IP.APP)}/${this.hardest(IP.APP)}`
      this.invBasicProp[0].POW = `${IP.POW}/${this.hard(IP.POW)}/${this.hardest(IP.POW)}`
      this.invBasicProp[0].INT = `${IP.INT}/${this.hard(IP.INT)}/${this.hardest(IP.INT)}`
      this.invBasicProp[0].LUK = `${IP.LUK}/${this.hard(IP.LUK)}/${this.hardest(IP.LUK)}`
    },
    getDeriveProp () {
      this.invDeriveProp[0].MOV = this.$store.state.investigator.invProperty.MOV
      this.invDeriveProp[0].SAN = this.$store.state.investigator.invProperty.SAN
      this.invDeriveProp[0].PH = this.$store.state.investigator.invProperty.PH
      this.invDeriveProp[0].HP = this.$store.state.investigator.invProperty.HP
      this.invDeriveProp[0].MA = this.$store.state.investigator.invProperty.MA
      this.invDeriveProp[0].DOD = this.$store.state.invSkills[10].total
      this.invDeriveProp[0].DB = this.$store.state.investigator.invProperty.DB
    },
    getWeapon () {
      this.invWeapon = this.$store.getters.invWeapon
    },
    getCheckbox () {
      this.checkbox = this.$store.state.invCheckbox
    },
    hard (num) {
      return Math.floor(num / 2)
    },
    hardest (num) {
      return Math.floor(num / 5)
    }
  }
}
</script>

<style scoped>
  ul {
    list-style: none;
    margin: 0;
    padding-left:10px;
  }
  .ul-container {
    background-color: #fff;
  }
  .header {
    background-color: #A0D5F6;
    color: #00243F;
    text-align: center;
    height: 35px;
    line-height: 35px;
  }
  .el-row {
    height: 100%;
  }
  .el-col {
    height: 100%
  }
  .invCard {
    height: 100%;
  }
  .invMessage {
    display: flex;
    flex-direction: column;
    height: 100%;
    margin-right: 1px;
  }
  .invMessage-top {
    flex: 1;
    display: flex;
  }
  .invMessage-top-left {
    flex: 1;
    margin-right: 1px;
    background-color: #fff;
  }
  .invBasic ul li {
    margin:0px 5px;
    padding-bottom: 10px;
  }
  .invCredit ul li {
    margin:0 5px;
    padding-bottom: 10px;
  }
  .invMessage-top-right {
    flex: 4;
    background-color: #fff;
  }
  .invBackground {
    background-color: #fff;
  }
  .invBackground-list {
    display: flex;
    flex-wrap: wrap;
  }
  .invBackground-ul {
    flex: 1;
  }
  .invBackground ul li {
    margin: 5px 5px 0 5px;
    padding-bottom: 10px;
  }
  .invMessage-mid-top {

  }
  .invMessage-mid-bottom {

  }
  .invSkill {

    height: 100%;
  }
  .skill-container {
    position: absolute;
    height: 100%;
    width: 25%;
    right: 0px;
  }
</style>



