// 数据组装
export default {
  invDB (state) {
    let StrAndSiz = state.investigator.invProperty.STR + state.investigator.invProperty.SIZ
    if (StrAndSiz >= 2 && StrAndSiz <= 64) {
      return '-2'
    } else if (StrAndSiz >= 65 && StrAndSiz <= 84) {
      return '-1'
    } else if (StrAndSiz >= 85 && StrAndSiz <= 124) {
      return '0'
    } else if (StrAndSiz >= 125 && StrAndSiz <= 164) {
      return '1d4'
    } else {
      return '1d6'
    }
  },
  invPH (state) {
    let StrAndSiz = state.investigator.invProperty.STR + state.investigator.invProperty.SIZ
    if (StrAndSiz >= 2 && StrAndSiz <= 64) {
      return '-2'
    } else if (StrAndSiz >= 65 && StrAndSiz <= 84) {
      return '-1'
    } else if (StrAndSiz >= 85 && StrAndSiz <= 124) {
      return '0'
    } else if (StrAndSiz >= 125 && StrAndSiz <= 164) {
      return '1'
    } else {
      return '2'
    }
  },
  invHP (state) {
    let ConAndSiz = state.investigator.invProperty.CON + state.investigator.invProperty.SIZ
    return Math.floor(ConAndSiz / 10)
  },
  invMOV (state) {
    if (state.investigator.invProperty.DEX < state.investigator.invProperty.SIZ && state.investigator.invProperty.STR < state.investigator.invProperty.SIZ) {
      return 7
    } else if (state.investigator.invProperty.DEX > state.investigator.invProperty.SIZ && state.investigator.invProperty.STR > state.investigator.invProperty.SIZ) {
      return 9
    } else {
      return 8
    }
  },
  invMA (state) {
    return (state.investigator.invProperty.POW / 5)
  },
  invSAN (state) {
    return state.investigator.invProperty.POW
  },
  skillsList (state) {
    let index = state.skillList.map(function (item) {
      let l = item.value.length - 1
      return item.value[l]
    })
    return index
  },
  invWeapon (state) {
    let Ws = state.investigator.invWeapon
    Ws[0].success = state.invSkills[Ws[0].index].total
    Ws[1].success = state.invSkills[Ws[1].index].total
    Ws[1].weapon = state.invSkills[Ws[1].index].name
    Ws[2].success = state.invSkills[Ws[2].index].total
    Ws[2].weapon = state.invSkills[Ws[2].index].name
    return Ws
  }
}



