<template>
  <div class="record">
    <div class="record-header">履历</div>
    <div class="form-container">
      <el-form :model="record" ref="record">
        <el-form-item
          label="形象描述"
          label-width="100px"
          prop="describe"
          size="mini">
        <el-input v-model="record.describe"></el-input>
        </el-form-item>
        <el-form-item
          label="思想与信念"
          label-width="100px"
          prop="belief"
          size="mini">
        <el-input v-model="record.belief"></el-input>
        </el-form-item>
        <el-form-item
          label="重要之人"
          label-width="100px"
          prop="person"
          size="mini">
        <el-input v-model="record.person"></el-input>
        </el-form-item>
        <el-form-item
          label="重要之物"
          label-width="100px"
          prop="treasure"
          size="mini">
        <el-input v-model="record.treasure"></el-input>
        </el-form-item>
        <el-form-item
          label="重要之地"
          label-width="100px"
          prop="place"
          size="mini">
        <el-input v-model="record.place"></el-input>
        </el-form-item>
        <el-form-item
          label="特质"
          label-width="100px"
          prop="peculiarity"
          size="mini">
        <el-input v-model="record.peculiarity"></el-input>
        </el-form-item>
        <el-form-item
          label="伤疤"
          label-width="100px"
          prop="scar"
          size="mini">
        <el-input v-model="record.scar"></el-input>
        </el-form-item>
        <el-form-item
          label="恐惧之物"
          label-width="100px"
          prop="fear"
          size="mini">
        <el-input v-model="record.fear"></el-input>
        </el-form-item>
      </el-form>
    </div>
    <div class="record-footer">
        <el-button type="primary" size="mini" @click="submitRecord">保存</el-button>
        <el-button type="danger" size="mini" @click="resetRecord('record')">重置</el-button>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      record: {
        describe: '',
        belief: '',
        person: '',
        treasure: '',
        place: '',
        peculiarity: '',
        scar: '',
        fear: ''
      }
    }
  },
  methods: {
    resetRecord (form) {
      this.$refs[form].resetFields()
    },
    submitRecord () {
      this.$store.commit('setRecord', this.record)
      alert('信息已保存')
    }
  }
}
</script>

<style scoped>
    .record-header {
      background-color: #A0D5F6;
      color: #00243F;
      height: 36px;
      line-height: 36px;
      border-radius:15px 15px 0 0;
      text-align: center;
    }
    .record-footer {
      background-color: #fff;
      text-align: center;
      padding:5px;
      border-radius: 0 0 15px 15px;
    }
    .form-container {
      padding: 20px;
      background-color: #fff;
    }
  </style>



