<template>
  <div class="derive">
    <div class="derive-header">衍生属性</div>
    <div class="table-container">
      <el-table :data="deriveProperty" width="100%" size="mini" stripe :header-cell-style="{backgroundColor: '#95c1e8',color: '#fff'}">
      <el-table-column label="属性" prop="props" align="center"></el-table-column>
      <el-table-column label="数值" prop="num" align="center"></el-table-column>
    </el-table>
    </div>
    <div class="tip-container">
      <div class="derive-tip">保存前请先计算</div>
    </div>
    <div class="derive-footer">
      <el-button type="success" @click="deriveProps" size="mini">计算</el-button>
      <el-button type="primary" @click="submitDeriveProps" size="mini">保存</el-button>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      deriveProperty: [
        {
          props: '伤害加值',
          num: '0'
        },
        {
          props: '体格',
          num: 0
        },
        {
          props: '移动',
          num: 0
        },
        {
          props: '生命',
          num: 0
        },
        {
          props: '理智',
          num: 0
        },
        {
          props: '魔法',
          num: 0
        }
      ]
    }
  },
  methods: {
    deriveProps () {
      this.deriveProperty[0].num = this.$store.getters.invDB
      this.deriveProperty[1].num = this.$store.getters.invPH
      this.deriveProperty[2].num = this.$store.getters.invMOV
      this.deriveProperty[3].num = this.$store.getters.invHP
      this.deriveProperty[4].num = this.$store.getters.invSAN
      this.deriveProperty[5].num = this.$store.getters.invMA
    },
    submitDeriveProps () {
      this.$store.commit('setDeriveProps', this.deriveProperty)
      alert('信息已保存')
    }
  }
}
</script>

<style scoped>
  .derive-header {
    background-color: #A0D5F6;
    color: #00243F;
    height: 36px;
    line-height: 36px;
    border-radius: 15px 15px 0 0;
    text-align: center;
  }
  .derive-tip {
    text-align: center;
    background-color: #fff;
    color: #ff0606;
  }
  .tip-container {
    background-color: #fff;
    padding-top: 167px;
  }
  .derive-footer {
    background-color: #fff;
    text-align: center;
    padding:5px 15px;
    border-radius: 0 0 15px 15px;
  }
</style>



