<template>
  <div class="property">
    <div class="container"><message></message></div>
    <div class="container"><roll></roll></div>
    <div class="container"><exact-props></exact-props></div>
    <div class="container"><derive></derive></div>
  </div>
</template>

<script>

import message from './message/message'
import roll from './roll/roll'
import exactProps from './exactProperty/exactProperty'
import derive from './derive/derive'
export default {
  components: {
    message,
    roll,
    exactProps,
    derive
  }
}
</script>

<style scoped>
  .property {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }
  .container {
    margin:5px;
  }
</style>



