<template>
  <div class="weapon">
    <div class="weapon-header">武器</div>
    <div class="weapon-container">
      <div class="weapons weaponOne">
          <div class="cascader">
            <span>武器一：</span>
            <el-cascader
            :options="weaponList"
            clearable
            :show-all-levels=false
            v-model="weapon[0].value"
            ></el-cascader>
          </div>
          <div class="form">
            <el-form>
              <el-form-item label="伤害" :label-width="formLabelWidth">
                <el-input v-model="weapon[0].damage"></el-input>
              </el-form-item>
              <el-form-item label="射程" :label-width="formLabelWidth">
                <el-input v-model="weapon[0].range"></el-input>
              </el-form-item>
              <el-form-item label="次数" :label-width="formLabelWidth">
                <el-input v-model="weapon[0].time"></el-input>
              </el-form-item>
              <el-form-item label="弹药" :label-width="formLabelWidth">
                <el-input v-model="weapon[0].ammo"></el-input>
               </el-form-item>
              <el-form-item label="故障率" :label-width="formLabelWidth">
                <el-input v-model="weapon[0].fault"></el-input>
              </el-form-item>
            </el-form>
          </div>
      </div>
      <div class="weapons weaponTwo">
            <div class="cascader">
              <span>武器二：</span>
              <el-cascader
              :options="weaponList"
              clearable
              :show-all-levels=false
              v-model="weapon[1].value"
              ></el-cascader>
            </div>
            <div class="form">
              <el-form>
                <el-form-item label="伤害" :label-width="formLabelWidth">
                  <el-input v-model="weapon[1].damage"></el-input>
                </el-form-item>
                <el-form-item label="射程" :label-width="formLabelWidth">
                  <el-input v-model="weapon[1].range"></el-input>
                </el-form-item>
                <el-form-item label="次数" :label-width="formLabelWidth">
                  <el-input v-model="weapon[1].time"></el-input>
                </el-form-item>
                <el-form-item label="弹药" :label-width="formLabelWidth">
                  <el-input v-model="weapon[1].ammo"></el-input>
                 </el-form-item>
                <el-form-item label="故障率" :label-width="formLabelWidth">
                  <el-input v-model="weapon[1].fault"></el-input>
                </el-form-item>
              </el-form>
            </div>
        </div>
    </div>
    <div class="weapon-footer">
      <el-button type="primary" size="mini" @click="submitWeapon">保存</el-button>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      formLabelWidth: '60px',
      weaponList: [
        {value: '59', label: '斧头'},
        {value: '61', label: '电锯'},
        {value: '62', label: '连枷'},
        {value: '63', label: '绞索'},
        {value: '64', label: '长矛'},
        {value: '65', label: '剑'},
        {value: '66', label: '鞭子'},
        {value: '67', label: '弓箭'},
        {value: '68', label: '手枪'},
        {value: '69', label: '重武器'},
        {value: '70', label: '喷火器'},
        {value: '71', label: '机枪'},
        {value: '72', label: '步枪'},
        {value: '73', label: '冲锋枪'}
      ],
      weapon: [
        {
          value: '',
          damage: '',
          range: '',
          time: '',
          ammo: '',
          fault: ''
        },
        {
          value: '',
          damage: '',
          range: '',
          time: '',
          ammo: '',
          fault: ''
        }
      ]
    }
  },
  methods: {
    submitWeapon () {
      this.$store.commit('setCareerWeapon', this.weapon)
      alert('信息已保存')
    }
  }
}
</script>

<style scoped>
  .weapon {
    font-size:14px;
  }
  .weapon-header {
    background-color: #A0D5F6;
    color: #00243F;
    height: 36px;
    line-height: 36px;
    border-radius:15px 15px 0 0;
    text-align: center;
  }
  .weapon-footer {
    background-color: #fff;
    text-align: center;
    padding:95px 0 15px 0;
    border-radius: 0 0 15px 15px;
  }
  .weapon-container {
    display: flex;
    background-color: #fff;
    padding-top: 15px;
  }
  .weapons {
    margin: 0 10px;
  }
  .cascader {
    margin: 10px 0;
  }
  .form {
    margin: 15px 0;
  }
</style>



