<template>
  <div class="navbar">
    <el-menu
      :default-active="activeIndex"
      class="el-menu"
      mode="horizontal"
      menu-trigger="click"
      background-color="#0B1C28"
      text-color="#7199A7"
      active-text-color="#DEF3FF"
      router
    >
      <el-menu-item index='1' route="/introduction">介绍</el-menu-item>
      <el-menu-item index='2' route="/appendix">附表</el-menu-item>
      <el-menu-item index='3' route="/create">创建</el-menu-item>
      <el-menu-item index='4' route="/investigator">人物界面</el-menu-item>
    </el-menu>
  </div>
</template>

<script>
export default {
  name: 'navBar',
  data () {
    return {
      activeIndex: '1'

    }
  }
}
</script>

<style scoped>
  .el-menu {
    border: none;
  }
  .navbar {
    width: 100%;
  }
</style>



