<template>
  <div class="credit">
    <div class="credit-header">信誉</div>
    <div class="form-container">
      <el-form
    :model="credit"
    ref="credit"
    :rules="rules"
    >
      <el-form-item
        label="信誉"
        label-width="80px"
        size="mini"
        prop="credit">
      <el-input v-model.number="credit.credit"></el-input>
      </el-form-item>
      <el-form-item
        label="生活水平"
        label-width="80px"
        size="mini"
        prop="lifeLevel">
      <el-input v-model.number="credit.lifeLevel"></el-input>
      </el-form-item>
      <el-form-item
        label="现金"
        label-width="80px"
        size="mini"
        prop="cash">
      <el-input v-model.number="credit.cash"></el-input>
      </el-form-item>
      <el-form-item
        label="其他资产"
        label-width="80px"
        size="mini"
        prop="deposit">
      <el-input v-model.number="credit.deposit"></el-input>
      </el-form-item>
      <el-form-item
        label="消费水平"
        label-width="80px"
        size="mini"
        prop="consumptionLevel">
      <el-input v-model.number="credit.consumptionLevel"></el-input>
      </el-form-item>
    </el-form>
    </div>
    <div class="credit-footer">
        <el-button type="primary" size="mini" @click="submitCredit">保存</el-button>
        <el-button type="danger" size="mini" @click="resetCredit('credit')">重置</el-button>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      rules: {
        credit: [
          {required: true, message: '信誉不能为空', trigger: 'blur'},
          {type: 'number', min: 0, max: 99, message: '信誉范围为0～99', trigger: 'blur'}
        ]
      },
      credit: {
        credit: 0,
        lifeLevel: '身无分文',
        cash: 0,
        deposit: 0,
        consumptionLevel: 0
      }
    }
  },
  methods: {
    resetCredit (form) {
      this.$refs[form].resetFields()
    },
    submitCredit () {
      this.$store.commit('setCredit', this.credit)
      alert('信息已保存')
    }
  }
}
</script>

<style scoped>
  .credit-header {
    background-color: #A0D5F6;
    color: #00243F;
    height: 36px;
    line-height: 36px;
    border-radius:15px 15px 0 0;
    text-align: center;
  }
  .credit-footer {
    background-color: #fff;
    text-align: center;
    padding:5px;
    padding-top: 146px;
    border-radius: 0 0 15px 15px;
  }
  .form-container {
    padding: 20px;
    background-color: #fff;
  }
</style>



