<template>
  <div class="career">
    <div class="container"><career-name></career-name></div>
    <div class="container"><career-skills></career-skills></div>
    <div class="container"><career-weapon></career-weapon></div>
  </div>
</template>

<script>
import careerName from './careerName/careerName'
import careerSkills from './careerSkills/careerSkills.vue'
import careerWeapon from './careerWeapon/careerWeapon'
export default {
  components: {
    careerName,
    careerSkills,
    careerWeapon
  }
}
</script>

<style scoped>
  .career {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }
  .container {
    margin: 5px;
  }
</style>



