<template>
  <div class="background">
    <div class="container"><credit></credit></div>
    <div class="container"><record></record></div>
    <div class="container"><rest></rest></div>
  </div>
</template>

<script>
import credit from './credit/credit'
import record from './record/record'
import rest from './rest/rest'
export default {
  components: {
    credit,
    record,
    rest
  },
  data () {
    return {

    }
  }
}
</script>

<style scoped>
  .background {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }
  .container {
    margin:5px;
  }
</style>



