export default {
  setInvBasicMessage (state, mes) {
    state.investigator.invMessage.playerName = mes.name
    state.investigator.invMessage.invName = mes.investigator
    state.investigator.invMessage.invCareer = mes.career
    state.investigator.invMessage.invAge = mes.age
    state.investigator.invMessage.invGender = mes.gender
    state.investigator.invMessage.invAddress = mes.address
    state.investigator.invMessage.invBackgrounds.invBackground = mes.background
  },
  setPropRoll (state, mes) {
    state.propertyRoll.STR = mes[0].num
    state.propertyRoll.SIZ = mes[1].num
    state.propertyRoll.EDU = mes[2].num
    state.propertyRoll.CON = mes[3].num
    state.propertyRoll.DEX = mes[4].num
    state.propertyRoll.APP = mes[5].num
    state.propertyRoll.POW = mes[6].num
    state.propertyRoll.INT = mes[7].num
    state.propertyRoll.LUK = mes[8].num
  },
  setPropCorrection (state, mes) {
    state.propertyCorrection.STR = mes.STR
    state.propertyCorrection.SIZ = mes.SIZ
    state.propertyCorrection.EDU = mes.EDU
    state.propertyCorrection.CON = mes.CON
    state.propertyCorrection.DEX = mes.DEX
    state.propertyCorrection.APP = mes.APP
    state.propertyCorrection.MOV = mes.MOV
  },
  setBasicProps (state, mes) {
    state.investigator.invProperty.STR = mes[0].num
    state.investigator.invProperty.SIZ = mes[1].num
    state.investigator.invProperty.EDU = mes[2].num
    state.investigator.invProperty.CON = mes[3].num
    state.investigator.invProperty.DEX = mes[4].num
    state.investigator.invProperty.APP = mes[5].num
    state.investigator.invProperty.POW = mes[6].num
    state.investigator.invProperty.INT = mes[7].num
    state.investigator.invProperty.LUK = mes[8].num
  },
  setDeriveProps (state, mes) {
    state.investigator.invProperty.DB = mes[0].num
    state.investigator.invProperty.PH = mes[1].num
    state.investigator.invProperty.MOV = mes[2].num
    state.investigator.invProperty.HP = mes[3].num
    state.investigator.invProperty.SAN = mes[4].num
    state.investigator.invProperty.MA = mes[5].num
  },
  setSkillPoints (state, mes) {
    state.investigator.invMessage.invSkillPoints = mes
  },
  setHobbyPoints (state, mes) {
    state.investigator.invMessage.invHobbyPoints = mes
  },
  setCareerName (state, mes) {
    state.investigator.invMessage.invCareer = mes.name
  },
  setCareerWeapon (state, mes) {
    state.investigator.invWeapon[1].index = mes[0].value[0]
    state.investigator.invWeapon[1].damage = mes[0].damage
    state.investigator.invWeapon[1].range = mes[0].range
    state.investigator.invWeapon[1].time = mes[0].time
    state.investigator.invWeapon[1].ammo = mes[0].ammo
    state.investigator.invWeapon[1].fault = mes[0].fault
    state.investigator.invWeapon[2].index = mes[1].value[0]
    state.investigator.invWeapon[2].damage = mes[1].damage
    state.investigator.invWeapon[2].range = mes[1].range
    state.investigator.invWeapon[2].time = mes[1].time
    state.investigator.invWeapon[2].ammo = mes[1].ammo
    state.investigator.invWeapon[2].fault = mes[1].fault
  },
  setSkillList (state, mes) {
    state.skillList = mes
  },
  setCredit (state, mes) {
    state.investigator.invMessage.invCredit = mes.credit
    state.investigator.invMessage.invLifeLevel = mes.lifeLevel
    state.investigator.invMessage.invCash = mes.cash
    state.investigator.invMessage.invDeposit = mes.deposit
    state.investigator.invMessage.invConsumptionLevel = mes.consumptionLevel
  },
  setRecord (state, mes) {
    state.investigator.invMessage.invBackgrounds.imageDescribe = mes.describe
    state.investigator.invMessage.invBackgrounds.invBelief = mes.belief
    state.investigator.invMessage.invBackgrounds.importPerson = mes.person
    state.investigator.invMessage.invBackgrounds.importPlace = mes.place
    state.investigator.invMessage.invBackgrounds.importTreasure = mes.treasure
    state.investigator.invMessage.invBackgrounds.invPeculiarity = mes.peculiarity
    state.investigator.invMessage.invBackgrounds.invScar = mes.scar
    state.investigator.invMessage.invBackgrounds.invFear = mes.fear
  },
  setRest (state, mes) {
    state.investigator.invMessage.invCompanions = mes.companions
    state.investigator.invMessage.invEquipments = mes.equipments
    state.investigator.invMessage.invBackgrounds.invOther = mes.other
  },
  setCheckbox (state, mes) {
    state.invCheckbox = mes
  },
  setSkill (state, mes) {
    state.investigator.invMessage.invCredit = mes[0].total
    state.invSkills = mes
  }
}



