<template>
  <div class="careerList">
      <el-table
        :data="tableData"
        size="mini"
        height="100%"
        :header-cell-style="{backgroundColor: '#95c1e8',color: '#fff'}"
        stripe
      >
    <el-table-column label="职业序号" prop='index' width="70"></el-table-column>
    <el-table-column label="职业" prop='name' width="150"></el-table-column>
    <el-table-column label="信誉" prop='credit' width="70"></el-table-column>
    <el-table-column label="信誉" prop='cAttr' width="200"></el-table-column>
    <el-table-column label="本职技能" min-width="300" prop='skills'></el-table-column>
  </el-table>
  </div>
</template>

<script>
import {getCareerList} from '../../../assets/js/careerList.js'

export default {
  data () {
    return {
      tableData: []
    }
  },
  mounted () {
    this.getCareers()
  },
  methods: {
    getCareers () {
      getCareerList().then(data => {
        this.settableData(data)
      })
    },
    settableData (data) {
      this.tableData = data.map(it => {
        const { index, name, credit, cAttr, skills } = it
        return {
          index,
          name,
          credit: `${credit.min}~${credit.max}`,
          cAttr,
          skills: skills.map(({ name }) => name).join(',')
        }
      })
    }
  }
}
</script>

<style scoped>
  .careerList {
    height:100%;
  }
</style>



