import Vue from 'vue'
import Router from 'vue-router'
import Introduction from '../pages/introduction/Introduction'
import Create from '../pages/create/Create'
import Appendix from '../pages/appendix/Appendix'
import Investigator from '../pages/investigator/Investigator'
import careerList from '../pages/appendix/careerList/careerList'
import Estate from '../pages/appendix/Estate/Estate'
import Property from '../pages/create/property/Property'
import Career from '../pages/create/career/Career'
import Background from '../pages/create/background/Background'
import Skills from '../pages/create/skills/Skills.vue'
import invCard from '../pages/investigator/invCard/invCard'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/introduction'
    },
    {
      path: '/introduction',
      name: 'Introduction',
      component: Introduction
    },
    {
      path: '/appendix',
      name: 'Appendix',
      component: Appendix,
      redirect: '/appendix/careerList',
      children: [
        {
          path: 'careerList',
          name: 'careerList',
          component: careerList
        },
        {
          path: 'estate',
          name: 'Estate',
          component: Estate
        }
      ]
    },
    {
      path: '/investigator',
      name: 'Investigator',
      component: Investigator,
      redirect: '/investigator/invCard',
      meta: {keepAlive: true},
      children: [
        {
          path: 'invCard',
          name: 'invCard',
          component: invCard
        }
      ]
    },
    {
      path: '/create',
      name: 'Create',
      component: Create,
      redirect: '/create/property',
      meta: {keepAlive: true},
      children: [
        {
          path: 'property',
          name: 'Property',
          component: Property
        },
        {
          path: 'career',
          name: 'Career',
          component: Career
        },
        {
          path: 'background',
          name: 'Background',
          component: Background
        },
        {
          path: 'skills',
          name: 'Skills',
          component: Skills
        }
      ]
    }
  ]
})



