<template>
  <div class="careerSkills">
    <div class="header">本职技能</div>
    <div class="cascader-container">
      <div class="cascader">
      <span>技能一：</span>
      <el-cascader
      :options="options"
      clearable
      :show-all-levels=false
      v-model="skillList[0].value"
      ></el-cascader>
    </div>
    <div class="cascader">
      <span>技能二：</span>
      <el-cascader
      :options="options"
      clearable
      :show-all-levels=false
      v-model="skillList[1].value"
      ></el-cascader>
    </div>
    <div class="cascader">
      <span>技能三：</span>
      <el-cascader
      :options="options"
      clearable
      :show-all-levels=false
      v-model="skillList[2].value"
      ></el-cascader>
    </div>
    <div class="cascader">
      <span>技能四：</span>
      <el-cascader
      :options="options"
      clearable
      :show-all-levels=false
      v-model="skillList[3].value"
      ></el-cascader>
    </div>
    <div class="cascader">
      <span>技能五：</span>
      <el-cascader
      :options="options"
      clearable
      :show-all-levels=false
      v-model="skillList[4].value"
      ></el-cascader>
    </div>
    <div class="cascader">
      <span>技能六：</span>
      <el-cascader
      :options="options"
      clearable
      :show-all-levels=false
      v-model="skillList[5].value"
      ></el-cascader>
    </div>
    <div class="cascader">
      <span>技能七：</span>
      <el-cascader
      :options="options"
      clearable
      :show-all-levels=false
      v-model="skillList[6].value"
      ></el-cascader>
    </div>
    <div class="cascader">
      <span>技能八：</span>
      <el-cascader
      :options="options"
      clearable
      :show-all-levels=false
      v-model="skillList[7].value"
      ></el-cascader>
    </div>
    </div>

    <div class="footer">
        <el-button type="primary" size="mini" @click="submitSkillList">保存</el-button>
    </div>
  </div>
</template>

<script>
import options from './careerSkills.js'
export default {
  data () {
    return {
      options,
      skillList: [
        {value: ''},
        {value: ''},
        {value: ''},
        {value: ''},
        {value: ''},
        {value: ''},
        {value: ''},
        {value: ''}
      ]
    }
  },
  methods: {
    submitSkillList () {
      this.$store.commit('setSkillList', this.skillList)
      alert('信息已保存')
    }
  }
}
</script>

<style scoped>
  .careerSkills {
    font-family: "PingFang SC";
    font-size: 14px;
    color: #606266;
  }
  .header {
    background-color: #A0D5F6;
    color: #00243F;
    height: 36px;
    line-height: 36px;
    border-radius:15px 15px 0 0;
    text-align: center;
  }
  .cascader-container {
    background-color: #fff;
    padding: 15px 0;
  }
  .cascader {
    margin: 15px 10px;
  }
  .footer {
    background-color: #fff;
    text-align: center;
    padding:0 10px 15px 10px;
    border-radius: 0 0 15px 15px;
  }
</style>



