<template>
  <div class="estate">
    <div class="table-container">
      <div class="table tableA">
        <div class="table-header"><span>1920s 美国（$）</span></div>
        <el-table
        stripe
        :data="nts"
        size="mini"
        style="width: 100%"
        height="85%"
        :header-cell-style="{backgroundColor: '#95c1e8',color: '#fff'}">
        <el-table-column prop="lifeLevel" label="生活水平"></el-table-column>
        <el-table-column prop="credit" label="信用(CR)"></el-table-column>
        <el-table-column prop="cash" label="现金"></el-table-column>
        <el-table-column prop="deposit" label="存款"></el-table-column>
        <el-table-column prop="consumptionLevel" label="消费水平"></el-table-column>
      </el-table>
      <div class="table-footer"><span>来自核心规则</span></div>
      </div>
    </div>
    <div class="table-container">
      <div class="table tableB">
        <div class="table-header"><span>1990+ 美国（$）</span></div>
        <el-table
        stripe
        :data="nns"
        size="mini"
        style="width: 100%"
        height="85%"
        :header-cell-style="{backgroundColor: '#95c1e8',color: '#fff'}">
          <el-table-column prop="lifeLevel" label="生活水平"></el-table-column>
          <el-table-column prop="credit" label="信用(CR)"></el-table-column>
          <el-table-column prop="cash" label="现金"></el-table-column>
          <el-table-column prop="deposit" label="存款"></el-table-column>
          <el-table-column prop="consumptionLevel" label="消费水平"></el-table-column>
        </el-table>
        <div class="table-footer"><span>来自核心规则</span></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Estate',
  data () {
    return {
      nts: [
        {
          lifeLevel: '身无分文',
          credit: '0',
          cash: '0.5',
          deposit: 'none',
          consumptionLevel: 0.5
        },
        {
          lifeLevel: '贫穷',
          credit: '1~9',
          cash: 'CR*1',
          deposit: 'CR*10',
          consumptionLevel: 2
        },
        {
          lifeLevel: '标准',
          credit: '10~49',
          cash: 'CR*2',
          deposit: 'CR*50',
          consumptionLevel: 10
        }, {
          lifeLevel: '小康',
          credit: '50~89',
          cash: 'CR*5',
          deposit: 'CR*500',
          consumptionLevel: 50
        }, {
          lifeLevel: '富裕',
          credit: '90~98',
          cash: 'CR*20',
          deposit: 'CR*2000',
          consumptionLevel: 250
        },
        {
          lifeLevel: '富豪',
          credit: '99',
          cash: '50000',
          deposit: '5亿+',
          consumptionLevel: 5000
        }
      ],
      nns: [
        {
          lifeLevel: '身无分文',
          credit: '0',
          cash: '10',
          deposit: 'none',
          consumptionLevel: 10
        },
        {
          lifeLevel: '贫穷',
          credit: '1~9',
          cash: 'CR*20',
          deposit: 'CR*200',
          consumptionLevel: 40
        },
        {
          lifeLevel: '标准',
          credit: '10~49',
          cash: 'CR*40',
          deposit: 'CR*1000',
          consumptionLevel: 200
        }, {
          lifeLevel: '小康',
          credit: '50~89',
          cash: 'CR*100',
          deposit: 'CR*10000',
          consumptionLevel: 1000
        }, {
          lifeLevel: '富裕',
          credit: '90~98',
          cash: 'CR*200',
          deposit: 'CR*40000',
          consumptionLevel: 5000
        },
        {
          lifeLevel: '富豪',
          credit: '99',
          cash: '1亿',
          deposit: '100亿+',
          consumptionLevel: 100000
        }
      ]
    }
  }
}
</script>

<style scoped>
  .estate {
    height: 100%;
    display: flex;
    flex-direction: column;
  }
  .table-container {
    height: 50%;
    flex: 1;
    margin: 20px;
    position: relative;
    box-sizing: border-box;
  }
  .table {
    position: absolute;
    height: 100%;
    width: 100%;
  }
  .table-header {
    padding-left: 20px;
    background-color: #A0D5F6;
    color: #00243F;
    border-radius: 15px 15px 0 0;
  }
  .table-footer {
    text-align: center;
    background-color: #fff;
    color: #00243F;
    border-radius: 0 0 15px 15px;
  }
</style>



