<template>
  <div class="roll">
      <div class="roll-header">属性掷骰</div>
      <el-table :data="rollProperty" width="100%" size="mini" stripe show-summary :header-cell-style="{backgroundColor: '#95c1e8',color: '#fff'}">
        <el-table-column label="属性" prop="props" width="82" align="center"></el-table-column>
        <el-table-column label="数值" prop="num" width="50" align="center"></el-table-column>
        <el-table-column label="操作" width="150" align="center">
          <template slot-scope="scope">
            <el-button size="mini" type="success" @click="roll(scope.$index)">生成</el-button>
          <el-button size="mini" type="danger" @click="clear(scope.$index)">清除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="roll-footer">
        <el-button type="primary" @click="submitRoll" size="mini">保存</el-button>
        <el-button type="success" @click="autoRoll" size="mini">自动生成</el-button>
        <el-button type="danger" @click="autoClear" size="mini">全部清除</el-button>
      </div>
  </div>
</template>

<script>
import rollProperty from './rollProperty.js'

export default {
  name: 'roll',
  data () {
    return {
      rollProperty
    }
  },
  methods: {
    DSix () {
      return Math.floor(Math.random() * (-5) + 6)
    },
    DTen () {
      return Math.floor(Math.random() * (-9) + 10)
    },
    DHundred () {
      return Math.floor(Math.random() * (-99) + 100)
    },
    fiveXTDSix () {
      return 5 * (this.DSix() + this.DSix() + this.DSix())
    },
    fiveXTDSixPSix () {
      return 5 * ((this.DSix() + this.DSix()) + 6)
    },
    EDUEnhance (ef) {
      let e0 = this.DHundred()
      if (e0 > ef) {
        ef = ef + this.DTen()
        if (ef > 99) {
          ef = 99
        }
      }
      return ef
    },

    roll (index) {
      if (this.invAge === 0) {
        alert('请先填写年龄')
      } else {
        if (index === 1 || index === 7) {
          this.rollProperty[index].num = this.fiveXTDSixPSix()
        } else if (index === 2) {
          if (this.invAge > 14 || this.invAge < 20) {
            this.rollProperty[index].num = this.fiveXTDSixPSix()
          } else if (this.invAge > 19 || this.invAge < 40) {
            this.rollProperty[index].num = this.EDUEnhance(this.fiveXTDSixPSix())
          } else if (this.invAge > 39 || this.invAge < 50) {
            this.rollProperty[index].num = this.EDUEnhance(this.EDUEnhance(this.fiveXTDSixPSix()))
          } else if (this.invAge > 49 || this.invAge < 60) {
            this.rollProperty[index].num = this.EDUEnhance(this.EDUEnhance(this.EDUEnhance(this.fiveXTDSixPSix())))
          } else {
            this.rollProperty[index].num = this.EDUEnhance(this.EDUEnhance(this.EDUEnhance(this.EDUEnhance(this.fiveXTDSixPSix()))))
          }
        } else if (index === 8) {
          if (this.invAge > 14 || this.invAge < 20) {
            this.rollProperty[index].num = Math.max(this.fiveXTDSix(), this.fiveXTDSix())
          } else {
            this.rollProperty[index].num = this.fiveXTDSix()
          }
        } else {
          this.rollProperty[index].num = this.fiveXTDSix()
        }
      }
    },
    autoRoll () {
      if (this.invAge === 0) {
        alert('请先填写年龄')
      } else {
        for (let i = 0; i < 9; i++) {
          this.roll(i)
        }
      }
    },
    clear (index) {
      this.rollProperty[index].num = 0
    },
    autoClear () {
      for (let i = 0; i < 9; i++) {
        this.clear(i)
      }
    },
    submitRoll () {
      this.$store.commit('setPropRoll', this.rollProperty)
      alert('信息已保存')
    }
  },
  computed: {
    invAge () {
      return this.$store.state.investigator.invMessage.invAge
    }
  }
}
</script>

<style scoped>
  .roll-header {
    background-color: #A0D5F6;
    color: #00243F;
    height: 36px;
    line-height: 36px;
    border-radius: 15px 15px 0 0;
    text-align: center;
  }
  .roll-footer {
    padding: 5px;
    background-color: #fff;
    text-align: center;
    border-radius: 0 0 15px 15px;
  }
</style>



