<template>
  <div class="skills">
    <div class="skills-header">
      <div class="tags">
        <el-tag type="primary">职业点数:{{careerPoint}}</el-tag>
        <el-tag type="danger">剩余职业点数:{{restCareerPoint}}</el-tag>
        <el-tag type="primary">兴趣点数:{{hobbyPoint}}</el-tag>
        <el-tag type="danger">剩余兴趣点数:{{restHobbyPoint}}</el-tag>
        <el-button type="primary" @click="submitSkill" size="mini">保存</el-button>
      </div>
    </div>
    <div class="table-container">
      <el-table
      :data="skills"
      size="mini"
      height=100%
      @selection-change="checked"
      :header-cell-style="{backgroundColor: '#95c1e8',color: '#fff'}"
      highlight-current-row
      stripe>
      <el-table-column type="selection" min-width="30" align="center"></el-table-column>
      <el-table-column label="序号" prop="index" min-width="15" align="center"></el-table-column>
      <el-table-column label="类别" prop="category" min-width="25" align="center"></el-table-column>
      <el-table-column label="技能" prop="name" min-width="25" align="center"></el-table-column>
      <el-table-column label="初始" prop="initial" min-width="15" align="center"></el-table-column>
      <el-table-column label="职业" prop="career" min-width="50" align="center">
        <template slot-scope="scope">
          <el-input-number
          v-model="skills[scope.$index].career"
          @change="careerCount(scope.$index)"
          size="mini"
          :disabled="skills[scope.$index].isDisabled"
          :max="99"></el-input-number>
        </template>
      </el-table-column>
      <el-table-column label="兴趣" prop="hobby" min-width="50" align="center">
        <template slot-scope="scope">
          <el-input-number
          v-model="skills[scope.$index].hobby"
          @change="hobbyCount(scope.$index)"
          size="mini"
          :min="0"
          :max="99"></el-input-number>
        </template>
      </el-table-column>
      <el-table-column label="合计" prop="total" min-width="15" align="center"></el-table-column>
      <el-table-column label="困难成功" prop="hard" min-width="20" align="center"></el-table-column>
      <el-table-column label="极难成功" prop="hardest" min-width="20" align="center"></el-table-column>
    </el-table>
    </div>
  </div>
</template>

<script>
import skills from './skills.js'
export default {
  data () {
    return {
      skills,
      careerPoint: (this.$store.state.investigator.invMessage.invSkillPoints - this.$store.state.investigator.invMessage.invCredit),
      hobbyPoint: this.$store.state.investigator.invMessage.invHobbyPoints,
      checkbox: []
    }
  },
  created () {
    this.numberInit()
  },
  mounted () {
    this.isDisabled()
  },
  methods: {
    checked (val) {
      this.checkbox = val
    },
    numberInit () {
      this.skills[0].initial = this.$store.state.investigator.invMessage.invCredit
      this.skills[10].initial = Math.floor(this.$store.state.investigator.invProperty.DEX / 2)
      this.skills[74].initial = this.$store.state.investigator.invProperty.EDU
      this.skills.map(function (item) {
        item.total = item.initial
      })
      for (let i = 0; i < this.skills.length; i++) {
        this.numCount(i)
      }
    },
    isDisabled () {
      const index = this.$store.getters.skillsList
      for (let i = 0; i < index.length; i++) {
        this.skills[index[i]].isDisabled = false
      }
    },
    careerCount (index) {
      this.numCount(index)
    },
    hobbyCount (index) {
      this.numCount(index)
    },
    numCount (index) {
      this.skills[index].total = this.skills[index].initial + this.skills[index].career + this.skills[index].hobby
      this.skills[index].hard = Math.floor(this.skills[index].total / 2)
      this.skills[index].hardest = Math.floor(this.skills[index].total / 5)
    },
    submitSkill () {
      this.$store.commit('setCheckbox', this.checkbox)
      this.$store.commit('setSkill', this.skills)
      alert('信息已保存')
    }
  },
  computed: {
    restCareerPoint: function () {
      let car = this.skills.map(function (item) {
        return item.career
      })
      let tol = car.reduce((pre, cur) => {
        return pre + cur
      })
      let res = this.careerPoint - tol
      if (res < 0) {
        alert('无剩余职业点可分配')
        return 'error'
      }
      return res
    },
    restHobbyPoint: function () {
      let hob = this.skills.map(function (item) {
        return item.hobby
      })
      let tol = hob.reduce((pre, cur) => {
        return pre + cur
      })
      let res = this.hobbyPoint - tol
      if (res < 0) {
        alert('无剩余兴趣点可分配')
        return 'error'
      } else {
        return res
      }
    }
  }
}
</script>

<style scoped>
  .skills {
    position: relative;
    height: 100%;
    width: 100%;
  }
  .table-container {
    position: absolute;
    height: 90%;
    width: 100%;
  }
  .skills-header {
    height: 10%;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
  }
</style>



