<template>
  <div id="app">
    <div class="g-container">
      <div class="header-container"><nav-bar></nav-bar></div>
      <div class="main-container"><keep-alive><router-view/></keep-alive></div>
    </div>
  </div>
</template>

<script>
import navBar from './components/navbar/navbar'

export default {
  name: 'App',
  components: {
    navBar
  },
  created () {
    const homePath = this.$router.currentRoute.path
    if (homePath !== '/introduction') {
      this.$router.push('/')
    }
  }
}
</script>

<style type="text/css">
  body {
    margin: 0;
    padding: 0;
  }
  #app {
    width: 100vw;
    height: 100vh;
    overflow: scroll;
    background:linear-gradient(bottom,#251C0E,#1D2A32);
    font-family: "PingFang SC";
    font-size: 14px;
    color: #606266;
  }
  .g-container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
  }
  .main-container {
    width: 100%;
    position: relative;
    flex: 1;
  }
</style>



