<template>
  <div class="invMessage">
    <div class="invMessage-header">调查员信息</div>
    <div class="form-container">
      <el-form :model="invMessage" ref="invMessage" :rules="rules">
        <el-form-item label="调查员" prop="name" :label-width="formLabelWidth">
          <el-input v-model="invMessage.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="玩家" prop="investigator" :label-width="formLabelWidth">
            <el-input v-model="invMessage.investigator" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="性别" prop="gender" :label-width="formLabelWidth">
          <el-select v-model="invMessage.gender" placeholder="请选择性别">
            <el-option label="男" value="男"></el-option>
            <el-option label="女" value="女"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="年龄" prop="age" :label-width="formLabelWidth">
            <el-input v-model.number="invMessage.age" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="住地" prop="address" :label-width="formLabelWidth">
            <el-input v-model.number="invMessage.address" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="出身" prop="background" :label-width="formLabelWidth">
            <el-input v-model="invMessage.background" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
    </div>
    <div class="invMessage-tip">保存前请先进行属性修正</div>
    <div class="invMessage-footer">
      <el-button type="primary" size="mini" @click="submitMessage()" :disabled="isDisabled">保存</el-button>
      <el-button type="info" size="mini" @click="dialogIsShow('invMessage')">属性修正</el-button>
      <el-button type="danger" size="mini" @click="resetMessage('invMessage')">重置</el-button>
    </div>
    <div class="correction-container">
        <el-dialog title="属性调整" :visible.sync="dialogFormVisible">
          <el-form label-width="90px">
            <el-form-item label="力量(STR)" v-if="propsVisible.strVisible">
              <el-input v-model.number="correction.STR" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="体质(CON)" v-if="propsVisible.conVisible">
              <el-input v-model.number="correction.CON" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="敏捷(DEX)" v-if="propsVisible.dexVisible">
              <el-input v-model.number="correction.EDU" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item >
              <span>合计<el-tag type="danger">{{this.correction.TOL}}</el-tag></span>
              <span>剩余<el-tag type="danger">{{restCount}}</el-tag></span>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false">取 消</el-button>
            <el-button type="primary" @click="restVerify">确 定</el-button>
          </div>
        </el-dialog>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      invMessage: {
        name: '',
        investigator: '',
        gender: '',
        age: '',
        address: '',
        background: ''
      },
      formLabelWidth: '80px',
      rules: {
        name: [
          { required: true, message: '请输入玩家名称', trigger: 'blur' }
        ],
        investigator: [
          { required: true, message: '请输入调查员名称', trigger: 'blur' }
        ],
        gender: [
          {required: true, message: '请选择性别', trigger: 'blur'}
        ],
        age: [
          {required: true, message: '请填写年龄', trigger: 'blur'},
          {type: 'number', max: 89, min: 15, message: '年龄不在符合区间内', trigger: 'blur'}
        ]
      },
      isDisabled: true,
      dialogFormVisible: false,
      correction: {
        STR: 0,
        SIZ: 0,
        EDU: 0,
        CON: 0,
        DEX: 0,
        APP: 0,
        MOV: 0,
        TOL: 0,
        RES: 0
      },
      propsVisible: {
        strVisible: false,
        conVisible: false,
        dexVisible: false
      }
    }
  },
  methods: {
    submitMessage () {
      this.$store.commit('setInvBasicMessage', this.invMessage)
      alert('信息已保存')
    },
    resetMessage (form) {
      this.$refs[form].resetFields()
      this.resetCorrection()
      this.isDisabled = true
    },
    resetCorrection () {
      this.correction.STR = 0
      this.correction.SIZ = 0
      this.correction.EDU = 0
      this.correction.CON = 0
      this.correction.DEX = 0
      this.correction.APP = 0
      this.correction.MOV = 0
      this.correction.TOL = 0
      this.correction.RES = 0
    },
    dialogIsShow (form) {
      this.$refs[form].validate((valid) => {
        if (valid) {
          this.ageJudge(this.invMessage.age)
          this.dialogFormVisible = true
          this.isDisabled = false
        } else {
          alert('请修改信息')
          return false
        }
      })
    },
    ageJudge (age) {
      if (age >= 15 && age <= 19) {
        this.correction.STR = 5
        this.correction.SIZ = 5
        this.correction.EDU = 5
        this.correction.TOL = 15
      } else if (age >= 20 && age <= 39) {
        this.correction.TOL = 0
      } else if (age >= 40 && age <= 49) {
        this.propsVisible.strVisible = true
        this.propsVisible.conVisible = true
        this.propsVisible.dexVisible = true
        this.correction.MOV = 1
        this.correction.APP = 5
        this.correction.TOL = 5
      } else if (age >= 50 && age <= 59) {
        this.propsVisible.strVisible = true
        this.propsVisible.conVisible = true
        this.propsVisible.dexVisible = true
        this.correction.MOV = 2
        this.correction.APP = 10
        this.correction.TOL = 10
      } else if (age >= 60 && age <= 69) {
        this.propsVisible.strVisible = true
        this.propsVisible.conVisible = true
        this.propsVisible.dexVisible = true
        this.correction.MOV = 3
        this.correction.APP = 15
        this.correction.TOL = 20
      } else if (age >= 70 && age <= 79) {
        this.propsVisible.strVisible = true
        this.propsVisible.conVisible = true
        this.propsVisible.dexVisible = true
        this.correction.MOV = 4
        this.correction.APP = 20
        this.correction.TOL = 40
      } else {
        this.propsVisible.strVisible = true
        this.propsVisible.conVisible = true
        this.propsVisible.dexVisible = true
        this.correction.MOV = 5
        this.correction.APP = 25
        this.correction.TOL = 80
      }
    },
    restVerify () {
      if (this.restCount < 0) {
        alert('你减得太狠了')
      } else if (this.restCount > 0) {
        alert('属性还得减')
      } else {
        alert('刚刚好')
        this.submitCorrection()
      }
    },
    submitCorrection () {
      this.$store.commit('setPropCorrection', this.correction)
      this.dialogFormVisible = false
    }
  },
  computed: {
    restCount: function () {
      let rest = this.correction.RES
      let total = this.correction.TOL
      rest = total - this.correction.STR - this.correction.SIZ - this.correction.EDU - this.correction.CON - this.correction.DEX
      return rest
    }
  }
}
</script>

<style scoped>
  .form-container {
    background-color: #fff;
    padding: 20px;
  }
  .invMessage-header {
    background-color: #A0D5F6;
    color: #00243F;
    height: 36px;
    line-height: 36px;
    border-radius: 15px 15px 0 0;
    text-align: center;
  }
  .invMessage-tip {
    text-align: center;
    background-color: #fff;
    color: #ff0606;
  }
  .invMessage-footer {
    background-color: #fff;
    text-align: center;
    padding:12px 5px 5px 5px;
    border-radius: 0 0 15px 15px;
  }
</style>



