<template>
  <div class="rest">
    <div class="rest-header">其他</div>
    <div class="form-container">
      <el-form :model="rest" ref="rest">
        <el-form-item
          label="调查员同伴"
          label-width="90px"
          prop="companions"
          size="mini">
        <el-input v-model="rest.companions"></el-input>
        </el-form-item>
        <el-form-item
          label="装备与道具"
          label-width="90px"
          prop="equipments"
          size="mini">
        <el-input v-model="rest.equipments"></el-input>
        </el-form-item>
        <el-form-item
          label="其他"
          label-width="90px"
          prop="other"
          size="mini">
        <el-input
        v-model="rest.other"
        type="textarea"
        :rows="6"
        ></el-input>
        </el-form-item>
      </el-form>
    </div>
    <div class="rest-footer">
        <el-button type="primary" size="mini" @click="submitRest">保存</el-button>
        <el-button type="danger" size="mini" @click="resetRest('rest')">重置</el-button>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      rest: {
        companions: '',
        equipments: '',
        other: ''
      }
    }
  },
  methods: {
    resetRest (form) {
      this.$refs[form].resetFields()
    },
    submitRest () {
      this.$store.commit('setRest', this.rest)
      alert('信息已保存')
    }
  }
}
</script>

<style scoped>
  .rest-header {
    background-color: #A0D5F6;
    color: #00243F;
    height: 36px;
    line-height: 36px;
    text-align: center;
    border-radius:15px 15px 0 0;
  }
  .rest-footer {
    background-color: #fff;
    text-align: center;
    padding:5px;
    padding-top: 149px;
    border-radius: 0 0 15px 15px;
  }
  .form-container {
    padding: 20px;
    background-color: #fff;
  }
</style>



