<template>
  <div class="careerMessage">
    <div class="careerMessage-header">职业信息</div>
    <div class="form-container">
      <el-form :model="career" ref="career" :rules="rules">
        <el-form-item label="职业" label-width="60px" prop="name" size="mini">
          <el-input v-model="career.name"></el-input>
        </el-form-item>
        <el-form-item label="力量" label-width="60px" size="mini">
          <el-input placeholder="请输入1～4的倍数" v-model.number="times.str" maxlength="1"></el-input>
        </el-form-item>
        <el-form-item label="敏捷" label-width="60px" size="mini">
          <el-input placeholder="请输入1～4的倍数" v-model.number="times.dex" maxlength="1"></el-input>
        </el-form-item>
        <el-form-item label="智力" label-width="60px" size="mini">
          <el-input placeholder="请输入1～4的倍数" v-model.number="times.int" maxlength="1"></el-input>
        </el-form-item>
        <el-form-item label="体质" label-width="60px" size="mini">
          <el-input placeholder="请输入1～4的倍数" v-model.number="times.con" maxlength="1"></el-input>
        </el-form-item>
        <el-form-item label="外貌" label-width="60px" size="mini">
          <el-input placeholder="请输入1～4的倍数" v-model.number="times.app" maxlength="1"></el-input>
        </el-form-item>
        <el-form-item label="意志" label-width="60px" size="mini">
          <el-input placeholder="请输入1～4的倍数" v-model.number="times.pow" maxlength="1"></el-input>
        </el-form-item>
        <el-form-item label="体型" label-width="60px" size="mini">
          <el-input placeholder="请输入1～4的倍数" v-model.number="times.siz" maxlength="1"></el-input>
        </el-form-item>
        <el-form-item label="教育" label-width="60px" size="mini">
          <el-input placeholder="请输入1～4的倍数" v-model.number="times.edu" maxlength="1"></el-input>
        </el-form-item>
      </el-form>
      <div class="careerMessage-tag">
        <el-tag type="danger">剩余:{{restTimePoint}}</el-tag>
        <el-tag type="primary">技能点:{{skillPoint}}</el-tag>
        <el-tag type="primary">兴趣点:{{hobbyPoint}}</el-tag>
      </div>
      <div class="careerMessage-footer">
        <el-button type="primary" @click="submitCareer" size="mini">保存</el-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      career: {
        name: ''
      },
      times: {
        str: '',
        dex: '',
        int: '',
        con: '',
        app: '',
        pow: '',
        siz: '',
        edu: ''
      },
      rules: {
        name: [
          {required: true, message: '职业不能为空', trigger: 'blur'}
        ]
      }
    }
  },
  computed: {
    restTimePoint: function () {
      let res = 4 - this.times.str - this.times.dex - this.times.int - this.times.con - this.times.app - this.times.pow - this.times.siz - this.times.edu
      if (res < 0) {
        alert('倍数总数不能超过4')
        return '错误'
      } else {
        return res
      }
    },
    skillPoint: function () {
      let invProps = this.$store.state.investigator.invProperty
      let points = invProps.STR * this.times.str + invProps.DEX * this.times.dex + invProps.INT * this.times.int + invProps.CON * this.times.con + invProps.APP * this.times.app + invProps.POW * this.times.pow + invProps.SIZ * this.times.siz + invProps.EDU * this.times.edu
      return points
    },
    hobbyPoint: function () {
      let points = this.$store.state.investigator.invProperty.INT * 2
      return points
    }
  },
  methods: {
    submitCareer () {
      this.$store.commit('setSkillPoints', this.skillPoint)
      this.$store.commit('setHobbyPoints', this.hobbyPoint)
      this.$store.commit('setCareerName', this.career)
      alert('信息已保存')
    }
  }
}
</script>

<style scoped>
  .careerMessage-header {
    background-color: #A0D5F6;
    color: #00243F;
    height: 36px;
    line-height: 36px;
    border-radius:15px 15px 0 0;
    text-align: center;
  }
  .careerMessage-tag {
    background-color: #fff;
    text-align: center;
  }
  .careerMessage-footer {
    background-color: #fff;
    text-align: center;
    margin: 5px;
    padding-top: 15px;

  }
  .form-container {
    background-color: #fff;
    padding: 10px;
    border-radius: 0 0 15px 15px;
  }
</style>



